/* tslint:disable:max-line-length */

import { NestedOption } from 'devextreme-angular/core';

import DevExpress from 'devextreme/bundles/dx.all';

export abstract class DxoFileManagerToolbar extends NestedOption {
    get fileSelectionItems(): Array<DevExpress.ui.dxFileManagerToolbarItem | string> {
        return this._getOption('fileSelectionItems');
    }
    set fileSelectionItems(value: Array<DevExpress.ui.dxFileManagerToolbarItem | string>) {
        this._setOption('fileSelectionItems', value);
    }

    get items(): Array<DevExpress.ui.dxFileManagerToolbarItem | string | DevExpress.ui.dxGanttToolbarItem | DevExpress.ui.dxHtmlEditorToolbarItem> {
        return this._getOption('items');
    }
    set items(value: Array<DevExpress.ui.dxFileManagerToolbarItem | string | DevExpress.ui.dxGanttToolbarItem | DevExpress.ui.dxHtmlEditorToolbarItem>) {
        this._setOption('items', value);
    }

    get container(): Element | JQuery | string {
        return this._getOption('container');
    }
    set container(value: Element | JQuery | string) {
        this._setOption('container', value);
    }

    get multiline(): boolean {
        return this._getOption('multiline');
    }
    set multiline(value: boolean) {
        this._setOption('multiline', value);
    }
}
